/* Heads up!
 * Neither Semantic UI nor Semantic UI React offer a responsive navbar, however, it can be implemented easily.
 * It can be more complicated, but you can create really flexible markup.
 */
class DesktopContainer extends Component {
  state = {}

  hideFixedMenu = () => this.setState({ fixed: false })
  showFixedMenu = () => this.setState({ fixed: true })

  render() {
    const { children } = this.props
    const { fixed } = this.state

    return (
      <Responsive minWidth={Responsive.onlyTablet.minWidth}>
        <Visibility
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.showFixedMenu}
        >
          <Segment
            textAlign='center'
            style={headerStyle}
            vertical
          >

            <Menu borderless
              size='large'
              pointing = {true}
              secondary = {true}
              style={{borderColor: "transparent", backgroundColor: "#ececec", color: headerFontColor, zIndex: '1000'}}
            >
              <Container>
                <Menu.Item as='a' active style={{borderColor: headerFontColor, color: headerFontColor }}>
                  Mirage
                </Menu.Item>
                <Menu.Item as='a' href="#about" style={{borderColor: headerBGColor, color: "#3f3f3f" }}>About</Menu.Item>
                <Menu.Item as='a' href="#careers" style={{borderColor: headerBGColor, color: "#3f3f3f"}}>Careers</Menu.Item>
                {/*
                <Menu.Item position='right'>
                  <Button href="" as='a'  style={{ marginLeft: '0.5em', color: "#3f3f3f" }}>
                    <Icon size={"large"} style={{marginLeft: "12px"}} name='sign in' />
                  </Button>
                  <Button href="" as='a' style={{ marginLeft: '0.5em', color: "#3f3f3f" }}>
                    <Icon size={"large"} style={{marginLeft: "12px"}} name='download' />
                  </Button>
                  <Button href="mailto:contact@projectmirage.me" as='a' style={{ marginLeft: '0.5em', color: "#3f3f3f" }}>
                    <Icon size={"large"} style={{marginLeft: "12px"}} name='mail' />
                  </Button>
                </Menu.Item>
                */}
              </Container>
            </Menu>
            <HomepageHeading />
            <Icon name="arrow down" size={"large"} style={{marginTop: "70px", color: "#3f3f3f"}}/>
          </Segment>
        </Visibility>

        {children}
      </Responsive>
    )
  }
}

DesktopContainer.propTypes = {
  children: PropTypes.node,
}

class MobileContainer extends Component {
  state = {}

  handlePusherClick = () => {
    const { sidebarOpened } = this.state

    if (sidebarOpened) this.setState({ sidebarOpened: false })
  }

  handleToggle = () => this.setState({ sidebarOpened: !this.state.sidebarOpened })

  render() {
    const { children } = this.props
    const { sidebarOpened } = this.state

    return (
      <Responsive maxWidth={Responsive.onlyMobile.maxWidth}>
        <Sidebar.Pushable>
          <Sidebar as={Menu} animation='uncover' inverted vertical visible={sidebarOpened}>
            <Menu.Item as='a' active>
              Mirage
            </Menu.Item>
            <Menu.Item as='a'>About</Menu.Item>
            <Menu.Item as='a'>Careers</Menu.Item>
            <Menu.Item as='a'>Log in</Menu.Item>
            <Menu.Item as='a'>Sign Up</Menu.Item>
          </Sidebar>

          <Sidebar.Pusher
            dimmed={sidebarOpened}
            onClick={this.handlePusherClick}
            style={{ minHeight: '100vh' }}
          >
            <Segment
              inverted
              textAlign='center'
              style={{ minHeight: 350, padding: '1em 0em', backgroundColor: headerBGColor }}
              vertical
            >
              <Container>
                <Menu inverted pointing secondary size='large' style={{borderColor: headerBGColor}}>
                  <Menu.Item onClick={this.handleToggle}>
                    <Icon name='sidebar' />
                  </Menu.Item>
                  <Menu.Item position='right'>
                    <Button as='a' style={{color: "#3f3f3f"}}>
                      Log in
                    </Button>
                    <Button as='a' style={{ marginLeft: '0.5em', color: "#3f3f3f" }}>
                      Sign Up
                    </Button>
                  </Menu.Item>
                </Menu>
              </Container>
              <HomepageHeading mobile />
            </Segment>

            {children}
          </Sidebar.Pusher>
        </Sidebar.Pushable>
      </Responsive>
    )
  }
}

MobileContainer.propTypes = {
  children: PropTypes.node,
}

const ResponsiveContainer = ({ children }) => (
  <div>
    <DesktopContainer>{children}</DesktopContainer>
  </div>
)

ResponsiveContainer.propTypes = {
  children: PropTypes.node,
}

